.prizeView {
  overflow: hidden;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  height: 130vh;
  width: 100%;
}
.prizeView .gifLayer {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
}
.prizeView .gifLayer > img {
  position: absolute;
}
.prizeView .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2%;
  height: 100%;
  width: 60%;
}
.prizeView .content .header {
  font-weight: 700;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 16px 0;
}
.prizeView .content .header .title {
  text-align: center;
  font-size: 54px;
}
.prizeView .content .header .subtitle {
  font-size: 24px;
}
.prizeView .content .prizeContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.prizeView .content .prizeContainer .first {
  margin-top: 100px;
}
.prizeView .content .prizeContainer .second {
  margin-top: 250px;
}
.prizeView .content .prizeContainer .third {
  margin-top: 400px;
}
.prizeView .content .prizeElement {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
}
.prizeView .content .prizeElement .prizePicture {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 200px;
  width: 100%;
}
.prizeView .content .prizeElement .prizePreview {
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 200px;
  width: 100%;
}
