.kindestView {
  font-family: 'Times New Roman';
  color: #f05757;
  overflow: hidden;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  height: 130vh;
  width: 100%;
  background: #e4baba;
  animation-name: pulse;
  animation-duration: 6s;
  animation-iteration-count: infinite;
}
.kindestView .header {
  font-weight: 700;
  display: flex;
  font-size: 72px;
  padding: 100px 0;
}
.kindestView table {
  animation-name: rainbow;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  font-size: 32px;
  min-width: 50%;
}
.kindestView table,
.kindestView td,
.kindestView tr,
.kindestView th {
  border: solid 1px black;
}
@keyframes pulse {
  0% {
    background-color: #e4baba;
  }
  50% {
    background-color: #d8a1a1;
  }
  100% {
    background-color: #e4baba;
  }
}
@keyframes rainbow {
  100%,
  0% {
    color: #ff0000;
    background-color: #e4baba;
  }
  8% {
    color: #ff7f00;
  }
  16% {
    color: #ffff00;
  }
  25% {
    color: #7fff00;
  }
  33% {
    color: #00ff00;
  }
  41% {
    color: #00ff7f;
  }
  50% {
    color: #00ffff;
    background-color: #d8a1a1;
  }
  58% {
    color: #007fff;
  }
  66% {
    color: #0000ff;
  }
  75% {
    color: #7f00ff;
  }
  83% {
    color: #ff00ff;
  }
  91% {
    color: #ff007f;
  }
}
