.movieView {
  overflow: hidden;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  height: 130vh;
  width: 100%;
}
.movieView .gifLayer {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
}
.movieView .gifLayer > img {
  position: absolute;
}
.movieView .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2%;
  height: 100%;
  width: 60%;
}
.movieView .content .header {
  font-weight: 700;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 16px 0;
}
.movieView .content .header .titleContainer {
  display: flex;
  align-items: center;
  gap: 16px;
}
.movieView .content .header .titleContainer .title {
  font-size: 40px;
}
.movieView .content .score {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 500px;
  font-size: 50px;
  width: 100px;
  height: 100px;
  border: solid 10px;
  margin: 16px;
}
.movieView .content .picture {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border: 6px solid;
  height: 60%;
  width: 100%;
  max-height: 600px;
  min-width: 600px;
}
.movieView .content .submitter {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 16px;
  font-size: 30px;
}
