.memoirView {
  font-family: 'Times New Roman';
  color: white;
  background: black;
  overflow: hidden;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  height: 130vh;
  width: 100%;
}
.memoirView .header {
  font-weight: 700;
  display: flex;
  font-size: 72px;
  padding: 100px 0;
}
.memoirView .headstones {
  display: flex;
  gap: 250px;
  font-size: 32px;
  align-items: flex-end;
  margin: 2% 0;
}
.memoirView .headstones > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
.memoirView .headstones img {
  max-height: 300px;
}
.memoirView .rip {
  margin-top: 6%;
  font-size: 120px;
}
