.finalScoresView {
  background: url('./trophybg.gif');
  font-family: 'Times New Roman';
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 100vh;
  width: 100%;
}
.finalScoresView .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.finalScoresView .content .header {
  background: white;
  font-weight: 700;
  display: flex;
  font-size: 72px;
  padding: 50px 20px;
}
.finalScoresView .content table {
  margin: 50px 0;
  background: white;
  font-size: 32px;
  min-width: 70%;
}
.finalScoresView .content table,
.finalScoresView .content td,
.finalScoresView .content tr,
.finalScoresView .content th {
  border: solid 1px black;
}
.finalScoresView .extraCredits {
  display: flex;
  font-size: 24px;
  font-weight: 700;
  white-space: pre;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
}
.finalScoresView .extraCredits span {
  padding: 10px 10px;
  background: white;
}
